import { noop } from 'lodash-es';
import React from 'react';
import type { RowsHeight, UpdateRowHeight } from '../types';

export const RowsClientHeightContext =
  React.createContext<[RowsHeight, UpdateRowHeight]>([{}, noop]);

export const useRowsClientHeight = () => {
  return React.useContext(RowsClientHeightContext);
};
